export class User {

    constructor(private name: string,
                private sessionId: string,
                private roles: [] ,
                private area: []) {
                }

    public getName() {
        return this.name;
    }

    public getSessionId() {
        return this.sessionId;
    }

    public getRoles() {
        return this.roles;
    }
    public getArea() {
        return this.area;
    }
}
