import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MustMatch } from '@app/utils/must-match.validator';
import { MatSort } from '@angular/material/sort';
import { Observable, from, Subscription } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ApiService } from '@app/config/api.service';
import { LayermsgService } from '@app/config/layermsg.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Role } from '@app/model/role';
import { Employee } from '@app/model/employee';
import { Supervisor } from '@app/model/supervisor';
import { EmployeeAddModalComponent } from '../employee-add-modal/employee-add-modal.component';
import { GlobalVariable } from '@app/config/global';
import { AppValidator } from '@app/config/app-validator';
import { OfficerService } from '@app/service/officer.service';
import { SupervisorService } from '@app/service/supervisor.service';
import { RoleService } from '@app/service/role.service';
import { EnumRole } from '@app/enumeration/enum-role.enum';
import { ReqExcluid } from '@app/request/req-excluid';
import { EnumStatus } from '@app/enumeration/enum-status.enum';
import { Parametries } from '@app/config/parametries';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-employee-add',
  templateUrl: './employee-add.component.html',
  styleUrls: ['./employee-add.component.scss']
})
export class EmployeeAddComponent implements OnInit, OnDestroy {

  formEmployAdd: FormGroup;
  formGroupSupervisor: FormControl = new FormControl('');
  selectedRole: string = '';
  checkedConfiguration: boolean = true;
  checkedExcluded: boolean = true;
  hide = true;
  hideConfirm = true;
  filteredOptions: Observable<Supervisor[]>;
  dataSource: MatTableDataSource<ReqExcluid>;
  displayedColumns = ['alias', 'mobile', 'action'];
  traceData;
  areas;
  supervisores;

  roleTypeFormControl: FormControl = new FormControl('', Validators.required);

  // @Author: elperez
  roles: Array<Role>;
  private listeners: Array<Subscription>;
  // ----------------

  @ViewChild(MatSort) sort: MatSort;

  constructor(
    public dialog: MatDialog,
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private officerService: OfficerService,
    private supervisorService: SupervisorService,
    private layerMsgService: LayermsgService,
    private roleService : RoleService,
    private router: Router
  ) {
    
    this.officerService.excluidsStatic.splice(0);
    this.listeners = new Array<Subscription>();
  }

  ngOnInit() {
    
    this.listeners.push(this.roleService.subscribe((roles) => {
      this.roles = roles;
    }));

    this.roleService.loadRoles();
    this.loadArea();
    this.loadAllSupervisor();

    this.formEmployAdd = this.formBuilder.group({
      name: ['', [
        Validators.required, 
        Validators.minLength(Parametries.NAME_PAT_MAT_MIN_LENGTH), 
        Validators.maxLength(Parametries.NAME_MAX_LENGTH), 
        Validators.pattern(Parametries.EMPLOYEE_ONLY_NUMBER_AND_CADENA)]],
      apePat: ['', [
        Validators.required, 
        Validators.minLength(Parametries.NAME_PAT_MAT_MIN_LENGTH), 
        Validators.maxLength(Parametries.APE_PAT_MAX_LENGTH), 
        Validators.pattern(Parametries.EMPLOYEE_ONLY_NUMBER_AND_CADENA)]],
      apeMat: ['', [
        Validators.required, 
        Validators.minLength(Parametries.NAME_PAT_MAT_MIN_LENGTH), 
        Validators.maxLength(Parametries.APE_MAT_MAX_LENGTH), 
        Validators.pattern(Parametries.EMPLOYEE_ONLY_NUMBER_AND_CADENA)]],
      email: ['', [
        Validators.required, 
        Validators.maxLength(Parametries.EMAIL_MAX_LENGTH), 
        Validators.email]],
      mobile: ['', [
        Validators.required, 
        Validators.minLength(Parametries.MOBILE_MIN_LENGTH), 
        Validators.maxLength(Parametries.MOBILE_MAX_LENGTH), 
        Validators.pattern(Parametries.PATTERN_NUMBER)]],
      phone: ['', [
        Validators.pattern(Parametries.EMPLOYEE_ONLY_NUMBER_AND_CADENA),
        Validators.minLength(Parametries.PHONE_MIN_LENGTH), 
        Validators.maxLength(Parametries.PHONE_MAX_LENGTH)]],
      user: ['', [
        
        Validators.minLength(Parametries.LOGIN_MIN_LENGTH), 
        Validators.maxLength(Parametries.LOGIN_MAX_LENGTH), 
        Validators.pattern(Parametries.PATTERN_LOGIN)]],
      password: ['', [
       
        Validators.pattern(Parametries.PATTERN_PASSWORD)]],
      confirmPassword: ['', [
        Validators.required]],
      roleType: ['', Validators.required],
      areaFk: ['', []],
      checkedConfiguration: [true],
      checkedExcluded: [true],
      employFk: this.formGroupSupervisor
    }, {
      validator: MustMatch('password', 'confirmPassword')
    });

    this.autocompleteSupervisor();

    this.officerService.excluids.subscribe(data => {
      
      this.dataSource = new MatTableDataSource(data);
    });
    this.setUserCategoryValidators() ;
  
  }

  ngOnDestroy() {
    this.listeners.forEach( (listener: Subscription) => {
      listener.unsubscribe();
    });
  }






  setUserCategoryValidators() {
    const confirmPassword = this.formEmployAdd.get('confirmPassword');
    const password = this.formEmployAdd.get('password') ;
    const username = this.formEmployAdd.get('user') ;

    this.formEmployAdd.get('roleType').valueChanges
      .subscribe(roleType => {

        if (roleType === 2) {


          
          confirmPassword.setValidators(  Validators.required     );
          password.setValidators([ Validators.required , Validators.pattern(Parametries.PATTERN_PASSWORD )]) ;
          username.setValidators([ Validators.required, Validators.minLength(Parametries.LOGIN_MIN_LENGTH), Validators.maxLength(Parametries.LOGIN_MAX_LENGTH), Validators.pattern(Parametries.PATTERN_LOGIN)]) ;
         
          
        }
        

        if (roleType === 3) {
        
          confirmPassword.setValidators(null);
          password.setValidators(null) ;
          username.setValidators(null) ;
         
        }




        confirmPassword.updateValueAndValidity();
        password.updateValueAndValidity() ;
        username.updateValueAndValidity() ;
       
      });
  }































  
  showPassword(e: any, field: string): void {
    e.preventDefault();
    if (field === 'hide') this.hide = !this.hide;
    if (field === 'hideConfirm') this.hideConfirm = !this.hideConfirm;
  }

  autocompleteSupervisor() {
    this.filteredOptions = this.formGroupSupervisor.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.nameEmploy),
        map(nameEmploy => nameEmploy ? this._filter(nameEmploy) : this.supervisores.slice())
      );
  }

  private _filter(name: string): Supervisor[] {
    return this.supervisores.filter((option: any) => option.nameEmploy.toLowerCase().indexOf(name.toLowerCase()) > -1);
  }

  displayFn(s?: Supervisor): string | undefined {
    return s ? s.nameEmploy : undefined;
  }

  get formGroup() {
    return this.formEmployAdd.controls
  }

  openDialog(excluid?: ReqExcluid) {

    let gen : ReqExcluid = excluid != null ? excluid : new ReqExcluid();

    const dialogRef = this.dialog.open(EmployeeAddModalComponent, {
      width: '300px',
      data: gen
    });
  }

  loadArea() {
    this.apiService.post('/area/v1/loadAllArea', {
      state: 'A'
    }, (result: any) => {
      this.areas = result.data;
    });
  }

  loadAllSupervisor() {
    this.supervisorService.loadAllSupervisor((result: any) => {
      this.supervisores = result.data;
    });
  }

  private addEmploy() {

    var employee: Employee = new Employee();
    employee.fromFormEmploy(this.formEmployAdd);

    if (this.formGroup.roleType.value === EnumRole.SUPERVISOR.code) {

      this.supervisorService.addSupervisor(employee.toSupervisor(), (result: any) => {
        this.layerMsgService.showMessageOk(EnumStatus.SAVE_OK.code, () => {
          this.router.navigate(['employee']);
        });
      });
    } else {

      employee.setExcluids(this.officerService.excluidsStatic);
      this.officerService.addOfficer(employee.toOfficer(), (result: any) => {
        this.layerMsgService.showMessageOk(EnumStatus.SAVE_OK.code, () => {
          this.router.navigate(['employee']);
        });
      });
    }
  }

  deleteExcluid(idExcluid: number){

    Swal.fire({
      title: '¬øDesea eliminar al excluido?',
      text: 'El excluido ser√° eliminado de la lista.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: ' <div style="margin: 0; padding: 0; display: flex; justify-content: center; align-items: center;"><span>Aceptar</span><img src="./../../../assets/icons/save.svg"></div>',
      confirmButtonColor :"#EF2A24",
      cancelButtonText: '<div style="margin: 0; padding: 0; display: flex; justify-content: center; align-items: center;"><span>Cancelar</span><img src="./../../../assets/icons/x-circle.svg"></div>'
    }).then((result) => {
      
      if (result.value) {

        for(let i = 0; i < this.officerService.excluidsStatic.length ; i++){
      
          if(this.officerService.excluidsStatic[i].idExcluid == idExcluid){
    
            this.officerService.excluidsStatic.splice(i, 1);
            this.dataSource = new MatTableDataSource(this.officerService.excluidsStatic);
            this.dataSource.sort = this.sort;
            break;
          }
        } 
      } else if (result.dismiss === Swal.DismissReason.cancel) {}
    })
  }

  onFormSubmit() {

    if (this.formEmployAdd.invalid) {

      return;
    } else {

      this.addEmploy();
    }
  }
}

