import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '@app/config/api.service';
import { FormControl } from '@angular/forms';
import { SharedService } from '@app/config/shared.service';
import { SharedVariable } from '@app/config/shared';
import { User } from '@app/model/user.class';


declare var $: any;
@Component({
	selector: 'app-chat',
	templateUrl: './chat.component.html',
	styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

	sectionView1 = true;
	sectionView2 = false;
	officers = [];
	officersTemp = [];
	nameOfficer;
	officer;
	contacts = [];
	contactsTemp = [];
	messages;
	tempMessages = false;
	customerSelect = '';
	customerSave;
	resultsMsg = [];
	idChat;
	indexCustomer;
	isLoadMore;
	page;
	allSupervisores;
	supervisores = [];
	allAreas;
	areas;

	private roles: {};
	rolePk : number;
	dataEmploy = null;
	
	constructor(
		private apiService: ApiService, 
		private router: Router, 
		private sharedService: SharedService) {
	}
	
	ngOnInit() {
		this.loadViewRole();
	}

	loadViewRole(){
		const user: User = this.sharedService.get(SharedVariable.USER);
		this.roles = user.getRoles();
		this.rolePk = this.roles[0].rolePk
		let name = JSON.parse(localStorage.getItem('user')).name;
		this.apiService.post('/employ/v1/loadDataEmploy', { "login": name }, (result: any) => {
			this.dataEmploy = result.data;
			switch (this.rolePk) {
				case 1:
					this.loadsupervisor();
					this.loadAllArea();
					this.isLoadMore = false;
					break;
				case 2:
					this.loadAllOfficerBySupervisorEmploy(this.dataEmploy.employPk);
					break;	
				case 3:
					this.toggleView();
					this.getContacts(this.dataEmploy.employPk,this.dataEmploy.nameEmploy);
					break;		
				default:
					break;
			}	
		}, (error) => {
			console.log(error);
	});	
	}

	selectOfficer(e, idOfficer, nameOfficer) {
		this.toggleView();
		this.getContacts(idOfficer, nameOfficer);
	}

	goBackView() {
		this.toggleView();
		this.messages = [];
		this.contacts = [];
		this.tempMessages = false;
	}

	getOfficer() {
		this.apiService.post('/employ/v1/loadAllOfficerEmploy', {}, (result: any) => {
				this.officers = result.data;
			});
	}

	getContacts(idOfficer, nameOfficer) {
		this.officer = idOfficer;
		this.indexCustomer = null;
		this.nameOfficer = nameOfficer
		this.apiService.post('/chat/v1/contacts', {
			"idOfficer": idOfficer
		}, (result: any) => {
			this.contacts = result.data;
			this.contactsTemp = result.data;
			this.messages = [];
			this.indexCustomer = null;
		}, (error) => {
			console.log(error);
		});
	}

	toggleView() {
		this.sectionView1 = !this.sectionView1;
		this.sectionView2 = !this.sectionView2;
	}

	searchContact(filterValue) {
		if (filterValue.length == 0) this.contacts = this.contactsTemp;
		this.contacts = this.contacts.filter(function (contact) {
			return contact.name.toLowerCase()
				&& contact.name.toLowerCase().indexOf(filterValue.trim().toLowerCase())
				!== -1;
		});
	}

	searchOfficers(filterValue) {
		if (filterValue.length == 0) this.officers = this.officersTemp;
		this.officers = this.officers.filter(function (itemOfficer) {
			return itemOfficer.nameEmploy.toLowerCase()
				&& itemOfficer.nameEmploy.toLowerCase().indexOf(filterValue.trim().toLowerCase())
				!== -1;
		});
	}

	loadMessages(customer, index) {
		this.customerSave = customer;
		let $this = this;
		this.indexCustomer = index;
		this.page = 0;
		this.messages = [];
		this.customerSelect = customer.name;
		this.idChat = customer.idChat;
		if (customer.pages.length > 0) {
			this.tempMessages = true;
			this.apiService.post('/chat/v1/messages', {
				"idChat": customer.idChat,
				"page": customer.pages[this.page++].date
			}, (result: any) => {
				this.messages = result.data;
				if (this.messages.length) {
					let lastMsg = this.messages[this.messages.length - 1];
					this.scrollWithJquery();
					this.contacts[this.indexCustomer].lastUpdate =
						lastMsg.msgDate + ' ' + lastMsg.msgHour;
				}
				setTimeout(function () {
					$this.autoload($this);
				}, 20);
			});
		}
	}

	autoload($this) {
		let el = $("#contentMsg")[0];
		el.scrollTop = el.scrollHeight;
		if (el.scrollHeight <= $(el).height()) {
			$this.loadMorePages(function () {
				setTimeout(function () {
					$this.autoload($this);
				}, 20);
			});
		}
	}

	loadMorePages(callback) {

		if (this.indexCustomer != null && this.page < this.contacts[this.indexCustomer].pages.length && !this.isLoadMore) {
			let customer = this.contacts[this.indexCustomer];
			if (customer.pages.length > 0) {
				let el = $("#contentMsg")[0];
				var scHeight = el.scrollHeight;
				this.isLoadMore = true;
				this.apiService.post('/chat/v1/messages', {
					"idChat": customer.idChat,
					"page": customer.pages[this.page++].date
				}, (result: any) => {
					this.messages = result.data.concat(this.messages);
					setTimeout(function () {
						el.scrollTop = el.scrollHeight - scHeight;
					}, 20);
					if (callback) {
						callback();
					}
					this.isLoadMore = false;
				}, (error) => {
					console.log(error);
					this.isLoadMore = false;
				});
			}
		}
	}

	reloadClients(e) {
		e.preventDefault();
		this.getContacts(this.officer, this.nameOfficer);
	}

	scrollWithJquery() {
		let $this = this;
		let el = $("#contentMsg")[0];

		$(el).scroll(function () {
			var height = el.scrollTop;
			if (height === 0) {
				$this.loadMorePages(null);
			}
		});
	}

	reloadMessages(e) {
		e.preventDefault();
		this.messages = [];
		let index = this.indexCustomer;
		this.indexCustomer = null;
		this.apiService.post('/chat/v1/pages', {
			"idChat": this.contacts[index].idChat
		}, (result: any) => {
			this.indexCustomer = index
			this.contacts[index].pages = result.data;
			this.loadMessages(this.customerSave, this.indexCustomer);
		}, (error) => {
			this.indexCustomer = index;
			console.log(error);
		});
	}

	searchMessage(filterValue) {
		this.resultsMsg = this.messages.filter(function (message) {
			if (filterValue) {
				return message.msgText && message.msgText.indexOf(filterValue) !== -1;
			}
		});
	}

	loadAllArea() {
		this.apiService.post('/area/v1/loadAllArea', {
			"state" : "A"
		}, (result: any) => {
				this.allAreas = result.data;
				this.areas = this.allAreas;

			}, (error) => {
				console.log(error);
			});
	}

	filterArea(filterValue: string) {
		
		this.areas = this.allAreas.filter(item => item.name.toLowerCase().indexOf(filterValue.toLowerCase()) > -1);
	}

	loadsupervisor() {
		this.apiService.post('/employ/v1/loadlAllSupervisorEmploy', {

		}, (result: any) => {
			this.allSupervisores = result.data;
			this.supervisores = this.allSupervisores;
		});
	}

	filterSupervisor(filterValue: string) {

		this.supervisores = this.allSupervisores.filter(item => item.nameEmploy.toLowerCase().indexOf(filterValue.toLowerCase()) > -1);
	}

	loadAllSupervisorAndOfficersByAreaEmploy(areaPk) {

		this.loadAllSupervisorByAreaEmploy(areaPk);
		this.loadAllOfficerByAreaEmploy(areaPk);

	}

	loadAllSupervisorByAreaEmploy(areaPk) {

		this.apiService.post('/employ/v1/loadAllSupervisorByAreaEmploy', {
			"areaPk": areaPk
		}, (result: any) => {
			this.allSupervisores = result.data;
			this.supervisores = this.allSupervisores;
		});
	}

	loadAllOfficerByAreaEmploy(areaPk) {

		this.apiService.post('/employ/v1/loadAllOfficerByAreaEmploy', {
			"areaPk": areaPk
		}, (result: any) => {
			this.officers = result.data;
		});
	}

	loadAllOfficerBySupervisorEmploy(employPk) {
		this.apiService.post('/employ/v1/loadAllOfficerBySupervisorEmploy', {
			"employPk": employPk
		}, (result: any) => {
				this.officers = result.data;
				this.officersTemp = result.data;
		}, (error) => {
			console.log(error);
		});
	}

}