import { FormGroup } from '@angular/forms';

export class Area {

    private areaPk: number;
    public name: string;
    public anexo: string;
    public mobile: string;
    public state: string;
    
    setAreaPk(areaPk: number){

        this.areaPk = areaPk;
    }

    private evalState(isActive: boolean){

        return isActive ? 'A' : 'B';
    }

    fromFormAreaAdd(formAreaAdd: FormGroup) {

        this.name = formAreaAdd.value.name.trim();
        this.anexo = formAreaAdd.value.anexo;
        this.mobile = formAreaAdd.value.mobile;
    }

    fromFormAreaUpdate(formAreaUpdate: FormGroup) {

        this.areaPk = formAreaUpdate.value.areaPk;
        this.name = formAreaUpdate.value.name.trim();
        this.anexo = formAreaUpdate.value.anexo;
        this.mobile = formAreaUpdate.value.mobile;
        this.state = formAreaUpdate.value.state;
    }

    toAreaAdd(){

        return {
            "areaName": this.name,
            "areaAnexo": this.anexo,
            "areaMobile": this.mobile
        };
    }

    toAreaUpdate(areaPk: number){

        return {
            "areaPk": areaPk,
            "areaName": this.name,
            "areaAnexo": this.anexo,
            "areaMobile": this.mobile,
            "areaState": this.state
        };
    }

}
